.pump {
  width: 411px;
  height: 636px;
}
.pump .title {
  width: 137px;
  height: 37px;
  font-size: 16px;
  text-align: center;
  line-height: 37px;
  letter-spacing: 1px;
  color: #ffffff;
  margin-bottom: 13px;
  overflow: hidden;
}
.pump .kuang {
  width: 411px;
  height: 589px;
  overflow: hidden;
}
.pump .kuang .bj {
  margin: 14px 0 0 13px;
  width: 392px;
  height: 566px;
  background-image: linear-gradient(90deg, rgba(2, 208, 255, 0.1) 0%, rgba(22, 32, 99, 0.1) 100%);
  overflow: hidden;
}
.pump .kuang .bj .drainage_one {
  margin: 35px 0 0 18px;
  width: 340px;
  height: 199px;
}
.pump .kuang .bj .drainage_one .tiele {
  width: 182px;
  height: 45px;
  font-size: 16px;
  color: #ffffff;
  text-align: center;
  line-height: 38px;
}
.pump .kuang .bj .drainage_one .condition {
  width: 290px;
  height: 36px;
  margin: 21px 0  0 27px;
}
.pump .kuang .bj .drainage_one .condition p {
  float: left;
  margin: 7px 0 0 0;
  display: inline-block;
  color: #00ffff;
  font-size: 14px;
  letter-spacing: 1px;
}
.pump .kuang .bj .drainage_one .condition .stop {
  margin-left: 18px;
  float: left;
  width: 17px;
  height: 36px;
}
.pump .kuang .bj .drainage_one .condition .stop span {
  display: inline-block;
  width: 17px;
  height: 22px;
}
.pump .kuang .bj .drainage_one .condition .stop i {
  font-size: 12px;
  font-style: normal;
  letter-spacing: 1px;
  color: #0090ff;
}
.pump .kuang .bj .drainage_one .jindu {
  margin-top: 13px;
  margin-left: 27px;
  width: 400px;
  height: 16px;
}
.pump .kuang .bj .drainage_one .jindu span {
  margin-right: 20px;
  font-size: 14px;
  color: #00ffff;
}
.pump .kuang .bj .drainage_one .jindu span i {
  font-style: normal;
  font-size: 22px;
  font-weight: 700;
}
